body {
  background: #57bd84;
  width: 80%;
  margin: 0 auto;
}

.App {
  text-align: center;
  width: 800px;
  margin: 0 auto;
}

.header {
  position: relative;
}

.header a {
  color: #000;
  text-decoration: none;
  font-size: 140px;
}

.list_day {
  display: flex;
  width: 100%;
  margin-top: 100px;
}

.list_day li {
  box-sizing: border-box;
  padding: 10px;
  list-style: none;
}
.list_day li:hover a {
  color: #fff;
  background: #000;
  border-color:#fff;
  box-shadow: 0 3px 0 0 #fff;
}
.list_day a {
  display: block;
  font-weight: bold;
  color: #131313;
  text-align: center;
  text-decoration: none;
  font-size: 50px;
  outline: none;
  user-select: none;
  font-family: 'Lato', sans-serif;
  letter-spacing: 0.04rem;
  padding: 1.5rem 3rem;
  cursor: pointer;
  border-radius: .4rem;
  border: 0.3rem solid #000;
  background: #fff;
  overflow: hidden;
  box-shadow: 0 3px 0 0 #000;
}


table {
  border-collapse: collapse;
  width: 100%;
}

table td {
  width: 25%;
  height: 70px;
  border: 3px solid #000;
  text-align: center;
  font-size: 1.2rem;
  background: #ccc;
}

table td:first-child {
  width: 10%;
}

button {
  padding: 10px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  border: 0 none;
  border-radius: 6px;
  padding: 10px 20px;
  color: #fff;
  background-color: tomato;
}